@import "normalize.css";

body {
  font-family: "Urbanist", sans-serif;
  background-color: #1a191f;
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.p-less {
  margin: 0 !important;
}

h1 {
  margin: 0;
  padding: 0;
  text-align: left;
  color: white;
  font-family: "Urbanist", sans-serif;
  font-weight: 100;
  letter-spacing: 4px;
}

.header {
  margin: 1rem 0;
}

.App {
  width: 100%;
  height: 100vh;
  height: -webkit-fill-available;
  padding-top: 3rem;
}

.pano-container {
  max-width: min(123vh, 80vw);
  margin: auto;
}

.panorama-wrapper2 {
  max-height: 80vh;
  max-width: 80vw;
  margin: auto;
}

.panorama-wrapper {
  height: 100%;
  width: auto;
  max-height: 80vh;
  aspect-ratio: 100/65;
  margin: auto;
}

.panorama-container {
  display: flex;
  position: relative;
  overflow: hidden;
  align-items: center;
  object-fit: contain;
  height: 100%;
  width: 100%;
}

.loading {
  opacity: 1;
  transition: opacity 0.5s ease;
  font-family: "Quicksand", sans-serif;
  font-weight: 100;
  color: white;
  position: absolute;
  display: flex;
  z-index: -2;
  align-items: center;
  width: 100%;
  height: 100%;
  justify-content: center;
}

.to-fade-in {
  opacity: 0;
  transition: opacity 3s ease;
}

.weird-decor {
  max-width: 60px;
  flex: 0.1;
  display:flex ;
  margin-top: 10px;
  margin-bottom: 10px;
}

.rect {
  min-width: 1px;
  max-width: 1px;
  background-color: #86828c;
  margin-left: 50%;
}

.caption {
  font-weight: 300;
  font-size: 14pt;
  flex: 1;
}

.specs-container{
  flex: 1;
}

.specs {
  white-space: pre-line;
  font-family: "Urbanist", sans-serif;
  font-weight: 100;
  font-size: 12pt;
  color: white;
  text-align: right;
}

.specs > p {
  margin-bottom: 0 !important;
}

.cred {
  font-family: "Urbanist", sans-serif;
  font-weight: 100;
  font-size: 10pt;
  color: #86828c;
  text-align: right;
}

.info {
  display: flex;
  letter-spacing: 2px;
}

.footer-wrapper {
  display: flex;
  margin-top: auto;
  margin-left: 5vw;
  margin-right: 5vw;
  margin-bottom: 2rem;
}

.footer-info {
  flex: 1;
  color: #86828c;
  font-size: 10pt;
  text-align: left;
}

.footer-icon {
  flex: 1;
  text-align: right;
  position: relative;
  margin: auto;
  letter-spacing: 4px;
}

.whitespacer {
  min-height: 10vh;
}

.f-icon {
  text-decoration: none !important;
  opacity: 0.5;
  padding-left: 0.5rem;
}

.f-icon:hover {
  opacity: 1 !important;
}

/* Large devices (desktops, less than 1200px) */
@media screen and (max-width: 1199.98px) {

}

/* Medium devices (tablets, less than 992px) */
@media screen and (max-width: 991.98px) {
  h1 {
    font-size: 27px;
  }

  .caption {
    font-size: 11pt;
  }

  .specs {
    font-size: 9pt;
  }

  .cred {
    font-size: 7pt;
  }

}

/* Small devices (landscape phones, less than 768px) */
@media screen and (max-width: 767.98px) {
  h1 {
    font-size: 22px;
    letter-spacing: 3px;
  }

  .caption {
    font-size: 9pt;
    letter-spacing: 1px;
  }

  .specs {
    font-size: 8pt;
    letter-spacing: 1px;
  }

  .cred {
    font-size: 6pt;
    letter-spacing: 1px;
  }

}
/* Extra small devices (portrait phones, less than 576px) */
@media screen and (max-width: 575.98px) {
  h1 {
    font-size: 18px;
    letter-spacing: 2px;
  }

  .caption {
    font-size: 7pt;
    letter-spacing: 1px;
  }

  .specs {
    font-size: 6pt;
    letter-spacing: 1px;
  }

  .cred {
    font-size: 4pt;
    letter-spacing: 1px;
  }
}